<template>
	<div>
		<div class="games">
			<div class="newgame" @click="$router.push('/newGame')">
				<mu-icon value="add" color="green" size="200"></mu-icon>New Game
			</div>
			<mu-card style="width: 275px;margin: 30px;" v-for="(card,idx) in games" :key="idx">
				<mu-card-header :title="card.info.name"></mu-card-header>
				<mu-card-media
					:title="'homeowner:' + card.homeowner"
					:sub-title="'state:' +  card.enemy ? 'gaming' : 'waiting'"
				>
					<img src="../assets/ship.jpg">
				</mu-card-media>
				<mu-card-actions @click="goGame(card.info,idx,card.enemy)">
					<mu-button color="primary">JOIN NOW</mu-button>
				</mu-card-actions>
			</mu-card>
		</div>
	</div>
</template>
<script>
export default {
	data() {
		return {
			games: [

			]
		}
	},
	created() {
		this.getList()
	},
	methods: {
		getList() {
			this.$http({
				url: '/rooms'
			}).then(res => {
				this.games = res
			})
		},
		goGame(info, id, enemy) {
			if (enemy) {
				this.$toast.error('This Room is FullÔºÅPlease choose another one!');
				return
			}
			sessionStorage.setItem('currRoom', id)
			sessionStorage.setItem('config', JSON.stringify(info))
			this.$router.push({
				path: '/game',
				query: { id, role: 'enemy' }
			})
		}
	}
}
</script>
<style lang="less" scoped>
.games {
	margin: 0 auto;
	padding-top: 50px;
	display: flex;
	flex-wrap: wrap;
	// justify-content: space-around;
	// padding: 50px 200px;
	width: 1010px;
	.newgame {
		width: 275px;
		height: 275px;
		display: flex;
		justify-content: center;
		align-items: center;
		flex-direction: column;
		background: white;
		margin: 30px;
		box-shadow: 0 2px 1px -1px rgba(0, 0, 0, 0.2),
			0 1px 1px 0 rgba(0, 0, 0, 0.14), 0 1px 3px 0 rgba(0, 0, 0, 0.12);
		cursor: pointer;
	}
}
</style>


